window.$ = window.jQuery = function (selectorOrArray) {
  //jQueryÂèØ‰ª•Êé•Êî∂‰∏Ä‰∏™ÈÄâÊã©Âô®ÊàñËÄÖÊï∞ÁªÑ
  let elements;
  //ÈÄöËøáÈáçËΩΩÔºåÂà§Êñ≠selectorOrArrayÊòØÈÄâÊã©Âô®ËøòÊòØÊï∞ÁªÑÔºåÂàÜÂà´ËµãÂÄºelements
  if (typeof selectorOrArray === "string") {
    //Â¶ÇÊûúselectorOrArrayÊòØÈÄâÊã©Âô®ÔºåÂàôelementsÊòØËøîÂõûÁöÑ‰∏Ä‰∫õÂÖÉÁ¥†
    elements = document.querySelectorAll(selectorOrArray);
  } else if (selectorOrArray instanceof Array) {
    //Â¶ÇÊûúselectorOrArrayÊòØÊï∞ÁªÑÔºåÂàôelementsÂ∞±ÊòØËøô‰∏™Êï∞ÁªÑ
    elements = selectorOrArray;
  }
  //apiÂèØ‰ª•Êìç‰Ωúelements
  return {
    //ÁÆÄÂÜôÊ≠•È™§2„ÄÅÂ∞Üconst api ={} ÊõøÊç¢‰∏∫return {}  ÂêåÊó∂ÁúÅÁï•ÊúÄÂêéÁöÑreturn api  Áõ¥Êé•Â∞ÜËØ•ÂØπË±°return
    //Èó≠ÂåÖÔºöÂáΩÊï∞ËÆøÈóÆÂ§ñÈÉ®ÂèòÈáè
    // addClass‰∏≠ËÆøÈóÆ‰∫ÜÂ§ñÈÉ®ÁöÑelementsÂèòÈáè
    addClass(className) {
      for (let i = 0; i < elements.length; i++) {
        elements[i].classList.add(className);
      }
      return this; //ËøîÂõûÁöÑÊòØapi,Âõ†‰∏∫addClassÂáΩÊï∞Ë∞ÉÁî®Êó∂ÊòØÈÄöËøáapi.addClassË∞ÉÁî®ÁöÑÔºåÂõ†Ê≠§thisÂ∞±ÊòØapi,ÂèØ‰ª•Áõ¥Êé•return this
      //ÁÆÄÂÜôÊ≠•È™§3„ÄÅÂΩìÁõ¥Êé•returnÂØπË±°Êó∂Ôºå‰∏çÁªôÂØπË±°ÂëΩÂêçÔºåÂàôËøôÈáåÂè™ËÉΩÂÜôreturn this Ôºå‰∏çËÉΩÂÜôreturn apiÔºåÂΩìÂáΩÊï∞Ë∞ÉÁî®Êó∂ÔºåthisÊåáÂêëË∞ÉÁî®ÂÆÉÁöÑapi
    },
    find(selector) {
      let array = [];
      for (let i = 0; i < elements.length; i++) {
        //jQuery('.test1').find('.child')
        //jQuery(ÈÄâÊã©Âô®)ÂÖàÊü•ÊâæËøîÂõû‰∫Ü‰∏Ä‰∫õÂÖÉÁ¥†elements
        //Ë∞ÉÁî®find(ÈÄâÊã©Âô®)Êó∂ÔºåÂÖàÈÅçÂéÜjQueryËøîÂõûÁöÑelements
        //Âú®ÊØè‰∏™element[i]‰∏ãÈù¢ÈÄöËøáquerySelectorAll(selector)Êü•ÊâæÁ¨¶ÂêàÁöÑÂÖÉÁ¥†
        //Áî±‰∫équerySelectorAllËøîÂõûÁöÑÊòØ‰∏ÄÁªÑ‰º™Êï∞ÁªÑÔºåÂõ†Ê≠§ÈúÄË¶Å Array.from()Â∞ÜËøîÂõûÁöÑ‰º™Êï∞ÁªÑËΩ¨‰∏∫ÁúüÊ≠£ÁöÑÊï∞ÁªÑÔºåÂπ∂ËµãÂÄºÁªôelements2
        const elements2 = Array.from(elements[i].querySelectorAll(selector));
        //ÈÄöËøáarray.concatÂ∞Üelements2‰∏éarrayËøûÊé•Ëµ∑Êù•ÔºåÂΩ¢ÊàêÊñ∞ÁöÑarray
        array = array.concat(elements2);
      }
      //return array    //Â¶ÇÊûúËøôÈáåÊòØreturn array ÔºåÂàôÂêéÈù¢ÁöÑ . Êó†Ê≥ïÁªßÁª≠ÈìæÂºèÊìç‰Ωú
      //const newApi = jQuery(array)   //ÈÄöËøájQueryÊûÑÈÄ†‰∏Ä‰∏™Êñ∞ÁöÑnewApi Âπ∂ËøîÂõû
      //return newApi     //Â¶ÇÊûúÁõ¥Êé•return api/this ÈÇ£‰πàÊØèÊ¨°ÂæóÂà∞Êñ∞ÁöÑÂÖÉÁ¥†ÈÉΩ‰ºöÊ±°Êüì‰πãÂâçÁöÑapi,ÊâÄ‰ª•ÂøÖÈ°ªÂæóÂà∞Êñ∞ÁöÑÂØπË±°newApi,ÈÅøÂÖçÂíåÂéüÊù•ÁöÑapiÁõ∏‰∫íÊ±°ÊüìÔºåÂΩ±ÂìçÂéüÂÖàÁöÑÂáΩÊï∞Ë∞ÉÁî®
      array.oldApi = this; //this==> ÂΩìÂâçapi===>ÊóßÁöÑ api
      return jQuery(array); //‰∏äÈù¢‰∏§Âè•ÂèØ‰ª•ÂêàÂπ∂ÁÆÄÂÜô‰∏∫Ëøô‰∏ÄÂè•„ÄÇ
      //ËøîÂõû‰∏Ä‰∏™Êñ∞ÁöÑapiÂØπË±°ÔºåÊù•Êìç‰Ωúarray,Êàë‰ª¨ÁªôjQuery()ÁöÑÂèÇÊï∞‰º†‰ªÄ‰πàÔºåjQueryÂ∞±‰ºöËøîÂõû‰∏Ä‰∏™ÂØπË±°Áî®Êù•Êìç‰Ωú‰ªÄ‰πà
    },

    each(fn) {
      //ÈÅçÂéÜÂΩìÂâçÁöÑÊâÄÊúâÂÖÉÁ¥†
      for (let i = 0; i < elements.length; i++) {
        fn.call(null, elements[i], i);
      }
      return this; //thisÂ∞±ÊòØapiÂØπË±°
    },
    parent() {
      //Ëé∑ÂèñÊØè‰∏™ÂÖÉÁ¥†ÁöÑÁà∂ÂÖÉÁ¥†
      const array = [];
      this.each((node) => {
        if (array.indexOf(node.parentNode) === -1) {
          //array.indexOf(node.parentNode) === -1 Ë°®Á§∫node.parentNode‰∏çÂú®arrayÊï∞ÁªÑ‰∏≠
          //Â¶ÇÊûúÊ≤°ÊúâÂàôÊ∑ªÂä†ÔºåÂéªÈáçÂ§ç
          array.push(node.parentNode);
        }
      });
      return jQuery(array); //ËøîÂõû‰∏Ä‰∏™ÂèØÊìç‰ΩúarrayÊï∞ÁªÑÁöÑjQueryÂØπË±°
    },
    children() {
      //Ëé∑ÂèñÊØè‰∏™ÂÖÉÁ¥†ÁöÑÂ≠êÂÖÉÁ¥†
      const array = [];
      this.each((node) => {
        array.push(...node.children); //Â±ïÂºÄÊìç‰ΩúÁ¨¶...ÁöÑ‰ΩúÁî®ÊòØÔºåÊäänode.childrenÈáåÈù¢ÁöÑÂÜÖÂÆπÊãÜÂºÄÔºå‰æùÊ¨°ÊîæÂÖ•array‰∏≠ÔºåÂæóÂà∞‰∏Ä‰∏™Âπ≥Èì∫ÁöÑÊï∞ÁªÑ
      });
      return jQuery(array); //ËøîÂõû‰∏Ä‰∏™ÂèØÊìç‰ΩúarrayÊï∞ÁªÑÁöÑjQueryÂØπË±°
    },
    siblings() {
      //Ëé∑ÂèñÊØè‰∏™ÂÖÉÁ¥†ÁöÑÂÖÑÂºüËäÇÁÇπ
      const array = [];
      this.each((node) => {
        array.push(
          ...Array.from(node.parentNode.children).filter((n) => n !== node)
        );
      });
      return jQuery(array); //ËøîÂõû‰∏Ä‰∏™ÂèØÊìç‰ΩúarrayÊï∞ÁªÑÁöÑjQueryÂØπË±°
    },
    index() {
      //Ëé∑ÂèñÊØè‰∏™ÂÖÉÁ¥†ÁöÑ‰ΩçÁΩÆ
      const array = [];
      this.each((node) => {
        let list = node.parentNode.children;
        let i;
        for (i = 0; i < list.length; i++) {
          if (list[i] === node) {
            break;
          }
        }
        array.push(i);
      });
      return jQuery(array); //ËøîÂõû‰∏Ä‰∏™ÂèØÊìç‰ΩúarrayÊï∞ÁªÑÁöÑjQueryÂØπË±°
    },
    next() {
      //Ëé∑ÂèñÊØè‰∏™ÂÖÉÁ¥†ÁöÑ‰∏ã‰∏Ä‰∏™ÂÖÑÂºüÂÖÉÁ¥†
      const array = [];
      this.each((node) => {
        let x = node.nextSibling;
        while (x && x.nodeType === 3) {
          //Â¶ÇÊûúxÂ≠òÂú®Âπ∂‰∏îxÁöÑËäÇÁÇπÁ±ªÂûãÊòØÊñáÊú¨ËäÇÁÇπÔºåÈÇ£‰πàÂ∞±ÁªßÁª≠Êâæ‰∏ã‰∏Ä‰∏™
          x = x.nextSibling;
        }
        array.push(x);
      });
      return jQuery(array); //ËøîÂõû‰∏Ä‰∏™ÂèØÊìç‰ΩúarrayÊï∞ÁªÑÁöÑjQueryÂØπË±°
    },
    prev() {
      //Ëé∑ÂèñÊØè‰∏™ÂÖÉÁ¥†ÁöÑ‰∏ã‰∏Ä‰∏™ÂÖÑÂºüÂÖÉÁ¥†
      const array = [];
      this.each((node) => {
        let x = node.previousSibling;
        while (x && x.nodeType === 3) {
          //Â¶ÇÊûúxÂ≠òÂú®Âπ∂‰∏îxÁöÑËäÇÁÇπÁ±ªÂûãÊòØÊñáÊú¨ËäÇÁÇπÔºåÈÇ£‰πàÂ∞±ÁªßÁª≠Êâæ‰∏ã‰∏Ä‰∏™
          x = x.previousSibling;
        }
        array.push(x);
      });
      return jQuery(array); //ËøîÂõû‰∏Ä‰∏™ÂèØÊìç‰ΩúarrayÊï∞ÁªÑÁöÑjQueryÂØπË±°
    },
    print() {
      //ÊâìÂç∞Âá∫ÂΩìÂâçÁöÑelements
      console.log(elements);
    },
    oldApi: selectorOrArray.oldApi, //Â∞Üarray‰∏≠ÁöÑoldApiËµãÂÄºÁªôapiÂØπË±°ÔºåËøôÊ†∑Âú®end()‰∏≠ÊâçËÉΩ‰ΩøÁî®Ëøô‰∏™oldApi

    end() {
      return this.oldApi; //this===> Êñ∞ÁöÑapi
    },
  };
  // return api   //ËøôÈáåÁöÑapi‰∏çËÉΩÊîπ‰∏∫thisÔºåÂõ†‰∏∫jQueryÂáΩÊï∞Ë∞ÉÁî®ÊòØÈÄöËøáwindow.jQueryË∞ÉÁî®ÁöÑÔºåthis => window,‰∏çÊòØapi
  //ÁÆÄÂÜôÊ≠•È™§1„ÄÅËøôÈáåreturn api ÂèØ‰ª•ÁúÅÁï•ÔºåÁõ¥Êé•Âú®ÂØπË±°Â£∞ÊòéÊó∂return
};
